.social {

    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        min-height: 140px;
        padding: 0 30px;   

        border-radius: 40px;
        background: url("/images/demo/underlay-red.png") lightgray 50% / cover no-repeat;
        box-shadow: 3px 3px 50px 0px rgba(0, 56, 89, 0.10);

        &-title {

            h2 {
                margin-bottom: 0;
                margin-left: 20px;
                position: relative;
                z-index: 1;
    
                &:before {
                    content: '';
                    display: block;
                    width: 70px;
                    height: 70px;
                    position: absolute;
                    z-index: -1;
                    top: -15px;
                    left: -20px;
                    flex-shrink: 0;
                    border-radius: 40px 0px 40px 40px;
                    background: #FFFBFB;
                }
            }
        }

        &-links {
            display: flex;
            gap: 10px;
        }

        &-phone {
            color: #3E5798;
            font-size: 28px;
            font-weight: 400;
            line-height: normal;
        }

        &-write {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            margin-top: 13px;
        }

        &-text {
            font-size: 16px;
            font-weight: 300;
            line-height: normal;
        }

        &-link {
            display: flex;
            align-items: center;
            column-gap: 6px;

            svg {
                width: 26px;
                height: 26px;
                path {
                    fill: #3E5798;
                }
            }
        }
    }
}

@media (max-width: 1199px) {

    .social {

        &__wrapper {
            width: 100%;
            height: auto;
            flex-direction: column;
            padding: 26px 0 50px 0;   
            border-radius: 20px;
            gap: 30px;

            .pre-last {
                order: 2;
            }
            
            .last {
                order: 1;
            }

            &-title {

                h2 {
                    margin-left: 0;
        
                    &:before {
                        width: 40px;
                        height: 40px;
                        top: -6px;
                        left: -15px;
                        border-radius: 20px 0px 20px 20px;
                    }
                }
            }

            &-write {
                justify-content: center;
                margin-top: 30px;
            }
        }
    }
}

@media (min-width: 1920px) {

    .social {

        &__wrapper {
            min-height: 175px;
            padding: 0 38px;   
    
            border-radius: 50px;
            box-shadow: 4px 4px 63px 0px rgba(0, 56, 89, 0.10);
    
            &-title {
    
                h2 {
                    margin-left: 25px;
        
                    &:before {
                        width: 88px;
                        height: 88px;
                        top: -19px;
                        left: -25px;
                        border-radius: 50px 0px 50px 50px;
                    }
                }
            }
    
            &-links {
                gap: 13px;
            }
    
            &-phone {
                font-size: 35px;
            }
    
            &-write {
                margin-top: 16px;
            }
    
            &-text {
                font-size: 20px;
            }
    
            &-link {
                column-gap: 8px;
    
                svg {
                    width: 33px;
                    height: 33px;
                }
            }
        }
    } 
}